.breed-select {
  margin-bottom: 10px;
}

.error, .loader {
  color: red;
  display: none;
}

.loader-icon {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-top: 20px;
  padding: 20px;
  display: none;
}

.cat-image {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.cat-info p {
  margin: 5px 0;
}

